import React, { useEffect, useRef, useState } from "react";
import { Chart, registerables } from "chart.js";
import "chartjs-adapter-moment";
import zoomPlugin from "chartjs-plugin-zoom";
import { chartConfig } from "./config";
import moment from "moment";

Chart.register(...registerables);
Chart.register(zoomPlugin);

function App() {
  const divRef = useRef<HTMLCanvasElement | null>(null);
  const [chart, setChart] = useState<Chart>();

  useEffect(() => {
    const chartInstance = new Chart(divRef.current, chartConfig);
    setChart(chartInstance);

    return () => chartInstance.destroy();
  }, []);

  const handle90daysClick = () => {
    chart?.zoomScale(
      "x",
      {
        min: moment().subtract(90, "d").valueOf(),
        max: moment().valueOf(),
      },
      "normal"
    );

    chart?.update();
  };

  const handle24daysClick = () => {
    chart?.zoomScale(
      "x",
      {
        min: moment().subtract(24, "h").valueOf(),
        max: moment().valueOf(),
      },
      "normal"
    );

    chart?.update();
  };

  const handleReset = () => chart?.resetZoom();

  return (
    <div
      style={{
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      <div style={{ display: "flex", margin: 10 }}>
        <button style={{ marginRight: "10px" }} onClick={handle90daysClick}>
          90 days
        </button>
        <button onClick={handle24daysClick} style={{ marginRight: "10px" }}>
          24 hours
        </button>
        <button onClick={handleReset}>reset</button>
      </div>
      <div style={{ width: 800, height: 600 }}>
        <canvas ref={divRef} />
      </div>
    </div>
  );
}

export default App;
